body {
  background-color: rgb(250, 250, 250);
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.image {
  width: 370px;
  margin: 10px 50px;
}

@media (max-width: 720px) {
  .container {
    margin: 0 10px;
  }
  .image {
    width: 100%;
  }
}
